body {
  margin: 0;
  font-family: Arial, sans-serif;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  background-color: red;
  color: #fff;
}

.navbar-logo {
  font-size: 1.5rem;
  color: yellow;
}

.navbar-links {
  list-style: none;
  display: flex;
}

.navbar-links li {
  margin-right: 20px;
}

.navbar-links a {
  text-decoration: none;
  color: green
}

.hamburger-menu {
  cursor: pointer;
  display: none;
}

.line {
  width: 25px;
  height: 3px;
  background-color: #fff;
  margin: 5px;
  transition: transform 0.4s;
}

.line.active.line1 {
  transform: rotate(-45deg) translate(-5px, 5px);
}

.line.active.line2 {
  opacity: 0;
}

.line.active.line3 {
  transform: rotate(45deg) translate(-5px, -5px);
}

@media (max-width: 768px) {
  .navbar-links {
    display: none;
    flex-direction: column;
    position: absolute;
    top: 70px;
    left: 0;
    width: 100%;
    background-color: pink;
    padding: 20px;
  }

  .navbar-links.active {
    display: flex;
  }

  .navbar-links li {
    margin-bottom: 10px;
  }

  .hamburger-menu {
    display: block;
  }
}
